import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/pages/index'
// import index from '@/pages/index/index'//
// import Rgroupdetail from '@/pages/index/rgroupdetail'
// import Rgroupinvite from '@/pages/index/rgroupinvite'//
// import Sgrouprule from '@/pages/index/sgrouprule'//
// import Buy from '@/pages/index/buy'//
// import Success from '@/pages/index/success'//
// import Coupon from '@/pages/index/coupon'//
Vue.use(Router)

let router= new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: resolve=> require(['@/pages/index'],resolve),
      children:[
        {path:'',component:resolve=> require(['@/pages/index/index'],resolve),meta:{title:''}}
      ]
    },
    {
      path:'/rgroupdetail',
      name:'rgroupdetail',
      component:resolve=> require(['@/pages/index/rgroupdetail'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/rgroupinvite',
      name:'rgroupinvite',
      component:resolve=> require(['@/pages/index/rgroupinvite'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/sgrouprule',
      name:'sgrouprule',
      component:resolve=> require(['@/pages/index/sgrouprule'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/buy',
      name:'buy',
      component:resolve=> require(['@/pages/index/buy'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/success',
      name:'success',
      component:resolve=> require(['@/pages/index/success'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/coupon',
      name:'coupon',
      component:resolve=> require(['@/pages/index/coupon'],resolve),
      meta:{
        title:''
      }
    },
    {
      path:'/rprotocol',
      name:'rprotocol',
      component:resolve=> require(['@/pages/index/rprotocol'],resolve),
      meta:{
        title:''
      }
    }

  ]
})

////
// to  from next() 
router.beforeEach((to, from, next) => {
  //title
  if(to.matched[0].name !== "index") {
    // common.checkRegisterStatus()
  }
  changeTitle(to.meta.title);
  next();
});

//title
function changeTitle(title) {
  title = title ? title : '';
  window.document.title = title;
};


export default router



